import React from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";

const Success = ({
  handleClose,
  handleSubmit,
  handleData,
  modalPage,
  setModalPage,
  incrementPage,
  decrementPage,
}) => (
  <div>
    <Modal show={true} centered onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>Vaa prijava je uspjeno poslana!</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <p>
          Vaa prijava je uspjeno poslana i zaprimljena. Kontaktirat emo Vas u
          najkraem roku.
        </p>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={handleClose}>Zatvori</Button>
        {modalPage > 1 && (
          <Button variant="secondary" onClick={decrementPage}>
            Nazad
          </Button>
        )}
        {modalPage < 4 && (
          <Button variant="primary" type="submit">
            Dalje
          </Button>
        )}
        {modalPage == 4 && (
          <Button variant="primary" onClick={incrementPage}>
            Poalji
          </Button>
        )}
      </Modal.Footer>
    </Modal>
  </div>
);

export default Success;
